.red {
  color: #000;
}

.red__item {
  color: red;
}

body {
  background-color: green;
  font-family: 'Roboto';
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

body {
  min-width: 320px;
  overflow-x: hidden;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

ul,
li {
  margin: 0;
  padding: 0;
}

body {
  line-height: 24px;
}

